  createLegend(json) {
    var legend = [];
    for (var i = 0; i < json["names"].length; i++) {
      var type = "fa fa-circle text-" + json["types"][i];
      legend.push(<i className={type} key={i} />);
      legend.push(" ");
      legend.push(json["names"][i]);
    }
    return legend;
  }

  getAllAccounts = () => {
    let self = this;
    axios.get(config.baseurl + '/accounts/fetchAllAccounts')
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ accountlist: response.data.rows })
        }
      });
  }

  fetchActivefilterData = () => {
    let self = this;
    axios.get(config.baseurl + '/filter/fetchActivefilterdata')
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ filterlist: response.data.rows })
          // console.log(response.data.rows.accountFromInfo,"filterlist----------------------");

          if (self.state.filterlist.length == 0) {
            self.setState({ errorMassege: true })
          }
        }
      });
  }

  fetchAllOpenTrade = () => {
    let self = this;
    axios.get(config.baseurl + '/accounts/fetchLastUpdatedTime')
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ dateTime: response.data.filteredInfoTime })
          // console.log(self.state.dataOpenTrade.accountFromInfo,"outyfjhkjh");
        }
      });
  }

  fetchStatusData = () => {
    let self = this;
    let tottal = 0;
    axios.get(config.baseurl + '/accounts/fetchStatusData')
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ fromAccounts: response.data.fromAccounts })
          self.setState({ fromSymbol: response.data.fromSymbol })
          // console.log(fromSymbol,"fromSymbol");
          self.setState({ toAccounts: response.data.toAccounts })
          self.setState({ toSymbol: response.data.toSymbol })
          self.setState({ fromOpenOrderInfos: response.data.fromOpenOrderInfos[0] })
          if (response.data.fromOpenOrderInfos.length > 0) {
            self.setState({ blockloader: false })
            if (response.data.fromOpenOrderInfos[0].order_type == 0) {
              self.setState({ total: response.data.fromSymbol.contract_size * (response.data.fromOpenOrderInfos[0].lots) })
            } else {
              self.setState({ total: response.data.fromSymbol.contract_size * -(response.data.fromOpenOrderInfos[0].lots) })
            }
            console.log(response.data.fromSymbol.contract_size * -(response.data.fromOpenOrderInfos[0].lots), "total");
            self.setState({ toOpenOrderInfos: response.data.toOpenOrderInfos[0] })
          }
          if (response.data.toOpenOrderInfos.length > 0) {
            if (response.data.toOpenOrderInfos[0].order_type == 0) {
              self.setState({ totalTo: response.data.toSymbol.contract_size * (response.data.toOpenOrderInfos[0].lots) })
            } else {
              self.setState({ totalTo: response.data.toSymbol.contract_size * -(response.data.toOpenOrderInfos[0].lots) })
            }
          }
          // let Finaltotal = {t}
          // console.log(self.state.tottal,"totalTo");

          // console.log(self.state.fromOpenOrderInfos.lots,"");
        }
      });
  }


